.fon img {
    width: 100%;
}

.descriptionBlock {
    padding: 10px;
    display: grid;
    grid-template-areas:
  "img info";
    grid-template-columns: 3fr 9fr;
}

.avatar {
    grid-area: img;
    max-width: 200px;
    max-height: 200px;
    border-radius: 50%;
}

.info{
    grid-area: info;
}

.contacts {
    padding-left: 10px;
}

.status{
    display: inline-block;
}

#more{
    display: none;
}